import React, { useState, useEffect, useRef } from "react";
import { useParams, useLocation } from "wouter";
import { useAuth } from "@/_core/hooks/useAuth";
import { trpc } from "@/lib/trpc";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Textarea } from "@/components/ui/textarea";
import { Badge } from "@/components/ui/badge";
import { toast } from "sonner";
import {
  Send,
  Paperclip,
  Sparkles,
  CheckCircle2,
  Clock,
  Calendar,
  Star,
  ArrowLeft,
  Loader2,
  Timer,
} from "lucide-react";
import { Streamdown } from "streamdown";

type ConsultationMessageRecord = {
  id: number;
  senderId: number;
  senderType: "client" | "consultant";
  message: string;
  createdAt: string;
  isAiAssisted?: boolean;
};

type BookingRecord = {
  ticketNumber?: string;
  subject?: string;
  description?: string;
  status?: string;
  consultantId?: number;
  consultant?: {
    id: number;
    userId: number;
    fullNameAr?: string;
  } | null;
  consultationType?: {
    id: number;
    nameAr: string;
    duration?: number;
    price?: number;
    slaHours?: number;
  } | null;
  userId?: number;
  createdAt?: string | Date;
  scheduledDate?: string | Date;
  scheduledTime?: string;
  price?: number;
  slaHours?: number;
  clientId?: number;
};

export default function ConsultationChat() {
  const { id } = useParams<{ id: string }>();
  const bookingId = parseInt(id || "0");
  const [, navigate] = useLocation();
  const { user, isAuthenticated } = useAuth();

  const [message, setMessage] = useState("");
  const [isLoadingAi, setIsLoadingAi] = useState(false);
  const [showRating, setShowRating] = useState(false);
  const [rating, setRating] = useState(0);
  const [ratingComment, setRatingComment] = useState("");

  const messagesEndRef = useRef<HTMLDivElement>(null);
  const utils = trpc.useUtils();

  // Get booking details
  const { data: bookingData, isLoading: bookingLoading } =
    trpc.consultant.getBooking.useQuery(
      { bookingId },
      { enabled: !!bookingId && isAuthenticated }
    );

  // Get messages
  const { data: messagesData, isLoading: messagesLoading } =
    trpc.consultant.getMessages.useQuery(
      { bookingId },
      {
        enabled: !!bookingId && isAuthenticated,
        refetchInterval: 3000, // Refresh every 3 seconds
      }
    );

  // Send message mutation
  const sendMessageMutation = trpc.consultant.sendMessage.useMutation({
    onSuccess: () => {
      setMessage("");
      utils.consultant.getMessages.invalidate({ bookingId });
      scrollToBottom();
    },
    onError: error => {
      toast.error("  : " + error.message);
    },
  });

  // Get AI suggestion mutation
  const getAiSuggestionMutation = trpc.consultant.getAiSuggestion.useMutation({
    onSuccess: data => {
      setMessage(
        Array.isArray(data.suggestion)
          ? JSON.stringify(data.suggestion)
          : data.suggestion
      );
      toast.success("   !");
      setIsLoadingAi(false);
    },
    onError: error => {
      toast.error("  : " + error.message);
      setIsLoadingAi(false);
    },
  });

  // Update status mutation
  const updateStatusMutation =
    trpc.consultant.updateConsultationStatus.useMutation({
      onSuccess: () => {
        toast.success("   ");
        utils.consulting.getTicket.invalidate({ ticketId: bookingId });
      },
      onError: error => {
        toast.error("  : " + error.message);
      },
    });

  // Rate consultation mutation
  const rateConsultationMutation = trpc.consultant.rateConsultation.useMutation(
    {
      onSuccess: () => {
        toast.success(" !");
        setShowRating(false);
        navigate("/my-consultations");
      },
      onError: error => {
        toast.error("  : " + error.message);
      },
    }
  );

  const booking = (bookingData?.booking ?? undefined) as BookingRecord | undefined;
  const calculateRemaining = () => {
    if (!booking?.createdAt) return null;
    const sla = booking.slaHours || booking.consultationType?.slaHours || 24;
    const due = new Date(booking.createdAt as any);
    due.setHours(due.getHours() + sla);
    const diffMs = due.getTime() - Date.now();
    if (diffMs <= 0) return "  ";
    const hours = Math.floor(diffMs / (1000 * 60 * 60));
    const minutes = Math.floor((diffMs % (1000 * 60 * 60)) / (1000 * 60));
    return `${hours}  ${minutes} `;
  };
  const messages: ConsultationMessageRecord[] = (messagesData?.messages ??
    []) as unknown as ConsultationMessageRecord[];

  // Check if user is consultant
  const [isConsultant, setIsConsultant] = useState(false);
  useEffect(() => {
    if (user && booking) {
      const consultantUserId = booking.consultant?.userId;
      const matchesId =
        consultantUserId === user.id || booking.consultantId === user.id;
      setIsConsultant(matchesId);
    }
  }, [user, booking]);

  // Scroll to bottom when messages change
  useEffect(() => {
    scrollToBottom();
  }, [messages]);

  const scrollToBottom = () => {
    messagesEndRef.current?.scrollIntoView({ behavior: "smooth" });
  };

  const handleSendMessage = async () => {
    if (!message.trim()) {
      toast.error("  ");
      return;
    }

    sendMessageMutation.mutate({
      bookingId,
      message: message.trim(),
    });
  };

  const handleGetAiSuggestion = async () => {
    if (messages.length === 0) {
      toast.error("     ");
      return;
    }

    setIsLoadingAi(true);

    // Get last client message
    const lastClientMessage = messages
      .filter(m => m.senderType === "client")
      .slice(-1)[0];

    if (!lastClientMessage) {
      toast.error("    ");
      setIsLoadingAi(false);
      return;
    }

    // Build conversation history
    const conversationHistory = messages.slice(-10).map(m => ({
      role: m.senderType as "client" | "consultant",
      message: m.message,
    }));

    getAiSuggestionMutation.mutate({
      bookingId,
      clientMessage: lastClientMessage.message,
      conversationHistory,
    });
  };

  const handleUpdateStatus = (status: "in-progress" | "completed") => {
    updateStatusMutation.mutate({ bookingId, status });
  };

  const handleSubmitRating = () => {
    if (rating === 0) {
      toast.error("  ");
      return;
    }

    rateConsultationMutation.mutate({
      bookingId,
      rating,
      comment: ratingComment,
    });
  };

  if (!isAuthenticated) {
    return (
      <div className="min-h-screen flex items-center justify-center">
        <Card className="w-full max-w-md">
          <CardContent className="pt-6">
            <p className="text-center text-muted-foreground">
                    
            </p>
          </CardContent>
        </Card>
      </div>
    );
  }

  if (bookingLoading || messagesLoading) {
    return (
      <div className="min-h-screen flex items-center justify-center">
        <Loader2 className="h-8 w-8 animate-spin text-primary" />
      </div>
    );
  }

  if (!booking) {
    return (
      <div className="min-h-screen flex items-center justify-center">
        <Card className="w-full max-w-md">
          <CardContent className="pt-6">
            <p className="text-center text-muted-foreground">
                
            </p>
          </CardContent>
        </Card>
      </div>
    );
  }

  const statusColors: Record<string, string> = {
    open: "bg-blue-500",
    pending: "bg-yellow-500",
    confirmed: "bg-blue-500",
    "in-progress": "bg-purple-500",
    completed: "bg-green-500",
    cancelled: "bg-red-500",
  };

  const statusLabels: Record<string, string> = {
    open: "",
    pending: " ",
    confirmed: "",
    "in-progress": " ",
    completed: "",
    cancelled: "",
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-blue-50 via-white to-purple-50">
      {/* Header */}
      <div className="bg-white border-b sticky top-0 z-10">
        <div className="container mx-auto px-4 py-4">
          <div className="flex items-center justify-between">
            <div className="flex items-center gap-4">
              <Button
                variant="ghost"
                size="icon"
                onClick={() =>
                  navigate(
                    isConsultant ? "/consultant/dashboard" : "/my-consultations"
                  )
                }
              >
                <ArrowLeft className="h-5 w-5" />
              </Button>
              <div>
                <h1 className="text-xl font-bold">
                   #{booking.ticketNumber || bookingId}
                </h1>
                <p className="text-sm text-muted-foreground">
                  {booking.subject || booking.consultationType?.nameAr}
                </p>
              </div>
            </div>
            <Badge className={statusColors[booking.status || "pending"]}>
              {statusLabels[booking.status || "pending"]}
            </Badge>
          </div>
          <div className="flex flex-wrap gap-3 text-xs text-muted-foreground mt-2">
            <span className="inline-flex items-center gap-1 bg-muted px-2 py-1 rounded">
              <Clock className="h-3 w-3" />
              SLA: {booking.slaHours || booking.consultationType?.slaHours || 24} 
            </span>
            <span className="inline-flex items-center gap-1 bg-muted px-2 py-1 rounded">
              <Timer className="h-3 w-3" />
              {calculateRemaining() || ""}
            </span>
            <span className="inline-flex items-center gap-1 bg-muted px-2 py-1 rounded">
              <Calendar className="h-3 w-3" />
              {booking.scheduledDate
                ? new Date(booking.scheduledDate).toLocaleDateString("ar-SA")
                : ""}{" "}
              {booking.scheduledTime || ""}
            </span>
          </div>
        </div>
      </div>

      <div className="container mx-auto px-4 py-6 max-w-5xl">
        <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
          {/* Main Chat Area */}
          <div className="lg:col-span-2">
            <Card className="h-[calc(100vh-200px)] flex flex-col">
              <CardHeader className="border-b">
                <CardTitle className="flex items-center gap-2">
                  <div className="h-3 w-3 rounded-full bg-green-500 animate-pulse" />
                   
                </CardTitle>
              </CardHeader>

              {/* Messages Area */}
              <CardContent className="flex-1 overflow-y-auto p-4 space-y-4">
                {messages.length === 0 ? (
                  <div className="flex flex-col items-center justify-center h-full text-center">
                    <div className="text-6xl mb-4"></div>
                    <h3 className="text-lg font-semibold mb-2">
                       
                    </h3>
                    <p className="text-muted-foreground">
                      {isConsultant
                        ? "     "
                        : "   "}
                    </p>
                  </div>
                ) : (
                  messages.map(msg => {
                    const isMyMessage = msg.senderId === user?.id;
                    return (
                      <div
                        key={msg.id}
                        className={`flex ${isMyMessage ? "justify-end" : "justify-start"}`}
                      >
                        <div
                          className={`max-w-[70%] rounded-lg p-3 ${
                            isMyMessage
                              ? "bg-primary text-primary-foreground"
                              : "bg-muted"
                          }`}
                        >
                          <div className="flex items-center gap-2 mb-1">
                            <span className="text-xs font-semibold">
                              {msg.senderType === "consultant"
                                ? ""
                                : ""}
                            </span>
                            {msg.isAiAssisted && (
                              <Sparkles className="h-3 w-3 text-yellow-500" />
                            )}
                          </div>
                          <Streamdown>{msg.message}</Streamdown>
                          <div className="text-xs opacity-70 mt-1">
                            {new Date(msg.createdAt).toLocaleTimeString(
                              "ar-SA",
                              {
                                hour: "2-digit",
                                minute: "2-digit",
                              }
                            )}
                          </div>
                        </div>
                      </div>
                    );
                  })
                )}
                <div ref={messagesEndRef} />
              </CardContent>

              {/* Input Area */}
              <div className="border-t p-4">
                {booking.status === "completed" &&
                !isConsultant &&
                !showRating ? (
                  <Button
                    className="w-full"
                    onClick={() => setShowRating(true)}
                  >
                    <Star className="mr-2 h-4 w-4" />
                     
                  </Button>
                ) : showRating ? (
                  <div className="space-y-4">
                    <div>
                      <label className="block text-sm font-medium mb-2">
                        
                      </label>
                      <div className="flex gap-2">
                        {[1, 2, 3, 4, 5].map(star => (
                          <button
                            key={star}
                            onClick={() => setRating(star)}
                            className="focus:outline-none"
                          >
                            <Star
                              className={`h-8 w-8 ${
                                star <= rating
                                  ? "fill-yellow-400 text-yellow-400"
                                  : "text-gray-300"
                              }`}
                            />
                          </button>
                        ))}
                      </div>
                    </div>
                    <Textarea
                      placeholder=" ()"
                      value={ratingComment}
                      onChange={e => setRatingComment(e.target.value)}
                      rows={3}
                    />
                    <div className="flex gap-2">
                      <Button
                        onClick={handleSubmitRating}
                        disabled={rateConsultationMutation.isPending}
                        className="flex-1"
                      >
                        {rateConsultationMutation.isPending ? (
                          <Loader2 className="mr-2 h-4 w-4 animate-spin" />
                        ) : (
                          <CheckCircle2 className="mr-2 h-4 w-4" />
                        )}
                         
                      </Button>
                      <Button
                        variant="outline"
                        onClick={() => setShowRating(false)}
                      >
                        
                      </Button>
                    </div>
                  </div>
                ) : booking.status !== "cancelled" ? (
                  <div className="space-y-2">
                    {isConsultant && (
                      <Button
                        variant="outline"
                        size="sm"
                        onClick={handleGetAiSuggestion}
                        disabled={isLoadingAi || messages.length === 0}
                        className="w-full mb-2"
                      >
                        {isLoadingAi ? (
                          <Loader2 className="mr-2 h-4 w-4 animate-spin" />
                        ) : (
                          <Sparkles className="mr-2 h-4 w-4" />
                        )}
                         AI -   
                      </Button>
                    )}
                    <div className="flex gap-2">
                      <Textarea
                        placeholder="  ..."
                        value={message}
                        onChange={e => setMessage(e.target.value)}
                        onKeyDown={e => {
                          if (e.key === "Enter" && !e.shiftKey) {
                            e.preventDefault();
                            handleSendMessage();
                          }
                        }}
                        rows={2}
                        className="flex-1"
                      />
                      <div className="flex flex-col gap-2">
                        <Button
                          size="icon"
                          onClick={handleSendMessage}
                          disabled={
                            sendMessageMutation.isPending || !message.trim()
                          }
                        >
                          {sendMessageMutation.isPending ? (
                            <Loader2 className="h-4 w-4 animate-spin" />
                          ) : (
                            <Send className="h-4 w-4" />
                          )}
                        </Button>
                      </div>
                    </div>
                  </div>
                ) : (
                  <div className="text-center text-muted-foreground py-4">
                     
                  </div>
                )}
              </div>
            </Card>
          </div>

          {/* Sidebar - Booking Details */}
          <div className="space-y-4">
            <Card>
              <CardHeader>
                <CardTitle className="text-lg"> </CardTitle>
              </CardHeader>
              <CardContent className="space-y-4">
                <div>
                  <label className="text-sm font-medium text-muted-foreground">
                     
                  </label>
                  <p className="font-medium">
                    {booking.consultationType?.nameAr || " "}
                  </p>
                </div>
                <div>
                  <label className="text-sm font-medium text-muted-foreground">
                    
                  </label>
                  <p className="font-medium">
                    {booking.subject || "  "}
                  </p>
                </div>
                <div>
                  <label className="text-sm font-medium text-muted-foreground">
                    
                  </label>
                  <p className="text-sm">{booking.description}</p>
                </div>
                <div className="grid grid-cols-2 gap-3">
                  <div>
                    <label className="text-sm font-medium text-muted-foreground">
                      
                    </label>
                    <p className="text-sm">
                      {booking.scheduledDate
                        ? new Date(booking.scheduledDate).toLocaleDateString("ar-SA")
                        : "-"}
                    </p>
                  </div>
                  <div>
                    <label className="text-sm font-medium text-muted-foreground">
                      
                    </label>
                    <p className="text-sm">{booking.scheduledTime || "-"}</p>
                  </div>
                </div>
                <div>
                  <label className="text-sm font-medium text-muted-foreground">
                     
                  </label>
                  <p className="text-sm">
                    {(booking.createdAt
                      ? new Date(booking.createdAt)
                      : new Date()
                    ).toLocaleDateString("ar-SA")}
                  </p>
                </div>
                <div className="grid grid-cols-2 gap-3">
                  <div>
                    <label className="text-sm font-medium text-muted-foreground">
                      SLA
                    </label>
                    <p className="text-sm font-semibold">
                      {booking.slaHours ||
                        booking.consultationType?.slaHours ||
                        "24"}{" "}
                      
                    </p>
                  </div>
                  <div>
                    <label className="text-sm font-medium text-muted-foreground">
                      
                    </label>
                    <p className="text-sm font-semibold text-primary">
                      {booking.price ||
                        booking.consultationType?.price ||
                        ""}{" "}
                      
                    </p>
                  </div>
                </div>
              </CardContent>
            </Card>

            {isConsultant &&
              booking.status !== "completed" &&
              booking.status !== "cancelled" && (
                <Card>
                  <CardHeader>
                    <CardTitle className="text-lg"></CardTitle>
                  </CardHeader>
                  <CardContent className="space-y-2">
                    {booking.status === "pending" && (
                      <Button
                        className="w-full"
                        onClick={() => handleUpdateStatus("in-progress")}
                        disabled={updateStatusMutation.isPending}
                      >
                        <Clock className="mr-2 h-4 w-4" />
                         
                      </Button>
                    )}
                    {booking.status === "in-progress" && (
                      <Button
                        className="w-full"
                        onClick={() => handleUpdateStatus("completed")}
                        disabled={updateStatusMutation.isPending}
                      >
                        <CheckCircle2 className="mr-2 h-4 w-4" />
                         
                      </Button>
                    )}
                  </CardContent>
                </Card>
              )}
          </div>
        </div>
      </div>
    </div>
  );
}
